.event-integration-box {
    #event-content p {
        font-size: 16px;
        line-height: 1.5;
        max-width: 700px;
    }

    .featured-image {
        width:100%;
        height: auto;
    }
}

#event-gallery-box {
    img {
        padding: 10px 10px 5px 5px;
    }
}

.message-success {
    color: #33cc33;
}

.accept.hidden, .deny.hidden {
	display: none;
}

.accept.button-primary {
    color: #fff;
    background-color: #5cb85c;
    border-color: #5cb85c;
    text-shadow: 0 -1px 1px #398439,1px 0 1px #398439,0 1px 1px #398439,-1px 0 1px #398439;
    box-shadow: 0 1px 0 #398439;
}

.accept.button-primary:hover, .accept.button-primary:focus, .accept.button-primary:active {
    color: #fff;
    background-color:#449d44;
    border-color:#398439;
}

.accept.button-primary:active {
    -webkit-box-shadow:inset 0 2px 0 #398439;
    box-shadow:inset 0 2px 0 #398439;
}

.deny.button-primary {
    color: #fff;
    background-color: #d9534f;
    border-color: #d9534f;
    text-shadow: 0 -1px 1px #ac2925,1px 0 1px #ac2925,0 1px 1px #ac2925,-1px 0 1px #ac2925;
    box-shadow: 0 1px 0 #ac2925;
}

.deny.button-primary:hover, .deny.button-primary:focus, .deny.button-primary:active {
    color: #fff;
    background-color:#c9302c;
    border-color:#ac2925;
}

.deny.button-primary:active {
    -webkit-box-shadow:inset 0 2px 0 #ac2925;
    box-shadow:inset 0 2px 0 #ac2925;
}

.import-buttons {
    margin-bottom: 8px;
}

.import-buttons .button-primary {
    display: block;
    padding: 4px 8px;
    line-height: normal;
    height: auto;
}

#draw-map-area {
    height: 440px;
    width: 100%;
    border: 1px solid #e5e5e5;
}

#clear-draw-map {
    margin-bottom: 10px;
}